<template>
    <section class="section detail" ref="detailRef">
        <loading v-if="loading"></loading>
        <note
                v-if="this.$store.state.details[this.$route.query.id]"
                ref="noteRef"
                :des="this.$store.state.details[this.$route.query.id].description"
                :author="this.$store.state.details[this.$route.query.id].owner.login"
                :avatar="this.$store.state.details[this.$route.query.id].owner.avatar_url"
                :time="this.$store.state.details[this.$route.query.id].created_at"
                :detail="this.$store.state.details[this.$route.query.id].files.blog.content"
        ></note>
    </section>
</template>

<script>
  import Note from '../components/Note'
  import Loading from '../components/Loading'

  export default {
    name: 'detail',
    components: {
      'note': Note,
      'loading': Loading
    },
    data() {
      return {
        blog: {},
        loading: false
      }
    },
    methods: {
      getBlog() {
        this.loading = true;
        this.$ajax.get(`https://api.github.com/gists/${this.$route.query.id}`).then((res) => {
          this.$store.commit('SET_DETAILS', {
            key: this.$route.query.id,
            value: res.data
          })
          this.loading = false;
          this.$nextTick(function () {
            this.$refs.detailRef.scrollTop = this.$store.state.scrollTops[this.$route.query.id]
          })
        })
      }
    },
    activated() {
      if(!this.$store.state.details[this.$route.query.id]) {
        this.getBlog()
      }
    },
    //记住滚动位置
    beforeRouteLeave(to, from, next) {
      this.$store.commit('SET_SCROLLTOPS', {
        key: this.$route.query.id,
        value: this.$refs.detailRef.scrollTop
      });
      next();
    },
    beforeRouteEnter(to, from, next) {
      next(vm => {
        vm.$refs.detailRef.scrollTop = vm.$store.state.scrollTops[vm.$route.query.id]
      })
    }
  }
</script>


